.main {
  max-height: 50vh;
  margin: 20px 0px;
}

.mainContent {
  display: flex;
  padding-bottom: 20px;
  border-bottom: 1px solid #fff;
}

.punkHighlight {
  flex: 0.25;
  display: flex;
  align-items: center;
  justify-content: center;
}

.punkContainer {
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  margin-right: 20px;
}

.selectedPunk {
  object-fit: contain;
  max-width: min(30vw, 40vh);
  max-height: 30vw;
}

.punkDetails {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0.75;
  color: #fff;
}

.title {
  font-size: 96px;
  font-weight: 800;
}

.itemNumber {
  color: #a1a5b0;
  font-size: 72px;
  align-self: center;
}

.Owner {
  display: flex;
  margin: 10px 0;
  height: 50px;
}

.owner > div {
  margin: 0 5px;
}

.ownerImageConatiner {
  height: 50px;
  width: 50px;
  border-radius: 50px;
  overflow: hidden;
  object-fit: contain;
}

.ownerImageConatiner > img {
  height: 100%;
  width: 100%;
}

.ownerDetails {
  display: flex;

  align-items: center;
  flex: 1;
}

.ownerNameAndHandle {
  flex: 1;
  /* margin-right: 250px; */
}

.ownerHandle {
  color: #00ebfe;
}
.ownerAddress {
  color: #fff;
}

.ownerLink {
  padding: 12px;
  border-radius: 50px;
  height: 30px;
  width: 30px;
  border: 1px solid white;
  margin: 0 0 0 10px;
}

.ownerLink > img {
  height: 100%;
  width: 100%;
}
